import {
	getRasumeEdu,
} from '@/api/job'
import {
	$wuxToast,
} from '../../../wxcomponents/wux/index'
export default {
	data() {
		return {
			list: null
		}
	},
	onShow(e) {
		uni.hideKeyboard()
		this.getRasumeEdu()
	},
	methods: {
		getRasumeEdu() {
			getRasumeEdu().then(response => {
				let r = response.data
				if (response.code === 20000) {
					if (r.list.length > 0) {
						this.list = r.list
					} else {
						uni.navigateTo({
							url: '/pages/resume/edu/add?list=2'
						});
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
					setTimeout(() => {
						uni.navigateBack();
					}, 1000)
				}
			})
		},
		addTo() {
			uni.navigateTo({
				url: '/pages/resume/edu/add'
			});
		},
		detailTo(id){
			uni.navigateTo({
				url: '/pages/resume/edu/detail?id=' + id
			});
		}
	}
}
